.nav {
  background-color: var(--gray-color-opacity);
  display: flex;

  @include tablet {
    display: flex;
  flex-direction: column;
  }

  &__list {
    position: relative;
    padding: 10px 40px;
    padding-right: 0;
    @include flex-v-center;
    justify-content: space-between;

    @include tablet {
      margin: 0;
      padding: 20px 0;

    }
}



}
.burger-wrap {

  display: none;

  @include tablet {
    display: block;
    border-radius: 5px;
    padding: 7px;
    background-color: var(--gray-light-color);
  }

}
.burger-wrap--close {
  border: none;
  padding: 7px;
  background-color: transparent;
}

.burger {
  @include burger;
  --burger-width: 44px;
  --burger-height: 30px;
  --burger-line-height: 3px;
  z-index: 5000;


  display: none;
  color: var(--light-color);

  @include tablet {
    display: block;
  }

  &__line {
    height: 4px;
    border-radius: 2px;
  }
}
.burger::before,
.burger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 4px;
  border-radius: 2px;
  background-color: #fff;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out, bottom 0.3s ease-in-out, background-color 0.3s ease-in-out;

}
.cart__count {
  padding-right: 40px;
}
.page_item {

    margin-right: 140px;

    @include big-desktop {
      margin-right: 80px;
    }
    @include for-desktop {
      margin-right: 65px;
    }
    @include tablet {
      margin: 0;
      padding: 20px 0;
    }

  a {
    font: var(--regular-lato);
    text-transform: capitalize;
    //font-size: 20px;
    color: var(--light-color);
    transition: color 0.3s ease-in-out;

    @include for-hover {
 &:hover {
  color: var( --text-light-color);
}
}



    &:active {
      color: var(--primary-color-hover);
    }
    &:focus-visible {
      outline: none;
      color: var(--primary-color-hover);
    }



    @include tablet {
      font-size: 35px;
    }

  }

  }





